@mixin list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin primary-button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 150px;
  padding: 6px 10px 10px;

  text-transform: lowercase;

  color: var(--base-weak);
  background-color: var(--accent);
  border: none;
}
